<template>
  <div class="caixa" role="main">
    <div class="caixatext" >
      <h2 class="titol negreta">Notícies</h2>
      <p class="textepetit">
        En aquest apartat trobaràs les últimes notícies sobre els àlbums més esperats i les novetats més destacades del món del pop.
      </p>
      <p class="textepetit">
        Descobreix llançaments exclusius, curiositats sobre les teves estrelles preferides i totes les tendències que marquen l'escena musical actual. 🎶✨
      </p>
    </div>

    <div class="card-container">
      <div v-for="proyecto in proyectos" :key="proyecto.id" class="card-wrapper">
        <Card2 
          :alt="proyecto.alt" 
          :titulo="proyecto.titulo" 
          :texto="proyecto.texto" 
          :url="proyecto.url" 
        />
      </div>
    </div>
  </div>
</template>

<script>
import Card2 from '/src/components/Card2.vue';
const lstProysJson = 'https://raw.githubusercontent.com/colausabater/practica2.musiccrave/refs/heads/main/artistes.json';

export default {
  name: 'app',
  components: {
    Card2,
  },
  data() {
    return {
      proyectos: [],
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      const res = await fetch(lstProysJson);
      const val = await res.json();
      this.proyectos = val;
    },
  },
};
</script>

<style>
.card-container {
  display: flex;
  flex-wrap: wrap; 
  justify-content: center; 
  gap: 1rem;
  margin-bottom: 2rem;
}

.card-wrapper {
  display: flex;
  justify-content: center;
  flex-shrink: 0; 
}

@media (max-width: 768px) {
  .card-container {
    flex-direction: column; 
    align-items: center;
  }
}
</style>
