<template>
  <div class="albums" role="main">
    <div class="caixatext">
      <h2 class="titol moviment negreta">Millors Àlbums</h2>
    <p class="moviment2 textepetit">Millors àlbums segons <span class="cursiva">Pitchfork</span> de la darrera dècada.</p>
    <p class="moviment textepetit">Posa't a sobre d'una imatge per veure més informació.</p>
    </div>

    <div class="image-container">
      <div
        v-for="(image, index) in images"
        :key="index"
        class="floating-image"
        :style="randomPositionStyle(index)"
        @mouseover="randomizePositions"
      >
        <img
          :src="image"
          :alt="(popovers[index])"
          class="chaotic-image"
        />
        <div class="popover">{{ popovers[index] }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      images: Array.from({ length: 15 }, (_, i) => require(`@/assets/imagen${i + 1}.jpg`)),
      popovers: [
        "Eternal Sunshine - Ariana Grande",
        "MOTOMAMI - ROSALÍA",
        "Melodrama - Lorde",
        "BRAT - Charli XCX",
        "Lo Que Te Conte Mientras Te Hacías La Dormida - La Oreja De Van Gogh",
        "HIT ME HARD AND SOFT - Billie Eilish",
        "El Mal Querer - ROSALÍA",
        "Sweetener - Ariana Grande",
        "Short n' Sweet - Sabrina Carpenter",
        "La Joia - Bad Gyal",
        "AMIGUES I AUTOTUNE - FADES",
        "ARTPOP - Lady Gaga",
        "Bodhiria - Judeline",
        "Eusexua - FKA Twigs",
        "Si Abro Los Ojos No Es Real - Amaia",
      ],
      positions: [],
    };
  },
  mounted() {
    this.randomizePositions();
    setInterval(this.randomizePositions, 2000);
  },
  methods: {
    randomizePositions() {
      this.positions = this.images.map(() => ({
        top: Math.random() * 80 + "%",
        left: Math.random() * 80 + "%",
        transform: `rotate(${Math.random() * 360}deg)`,
      }));
    },
    randomPositionStyle(index) {
      const position = this.positions[index] || {};
      return {
        position: "absolute",
        transition: "all 1s ease",
        ...position,
      };
    },
  },
};
</script>

<style>
.albums {
  font-family: Arial, sans-serif;
  overflow: hidden;
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.image-container {
  position: relative;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.floating-image {
  pointer-events: auto;
  position: absolute;
  text-align: center;
}

.chaotic-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.popover {
  position: absolute;
  top: -30px; 
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 12px;
  white-space: nowrap;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
}

.floating-image:hover .popover {
  visibility: visible;
  opacity: 1;
}

.subratllat a {
  color: #1183FF;
  text-decoration: underline;
}

.moviment {
  animation: shake 0.5s infinite;
}

.moviment2 {
  animation: shake 0.4s infinite;
}

@keyframes shake {
  0% { transform: translate(0, 0); }
  25% { transform: translate(-5px, 5px); }
  50% { transform: translate(5px, -5px); }
  75% { transform: translate(-5px, -5px); }
  100% { transform: translate(0, 0); }
}
</style>
