<template>
  <div class="caixa" role="main">
    <div class="textecontacte">
      <h2 class="titol negreta">Contacte</h2>
      <p class="textepetit">Subscriu-te per rebre les últimes novetats i notificacions directament al teu correu electrònic!</p>
      <p class="textepetit">El ratolí està deshabilitat, pots utilitzar el teclat per omplir el formulari.</p>
    </div>

    <div class="form-container" :style="isDisabled ? { pointerEvents: 'none' } : {}">
      <div class="form-card">
        <h3 class="form-header">Formulari Newsletter</h3>
        <p v-if="timerActive" class="gris">El formulari es deshabilitarà en <strong class="vermell">{{ countdown }}</strong> segons.</p>
        
        <form @submit="onSubmit" @reset="onReset" v-if="show">
          <!-- Nom -->
          <div class="form-group">
            <label for="input-1">Nom</label>
            <input
              type="text"
              id="input-1"
              v-model="form.firstName"
              placeholder="Escriu el teu nom"
              required
              class="form-input"
            />
          </div>

          <!-- Cognoms -->
          <div class="form-group">
            <label for="input-2">Cognoms</label>
            <input
              type="text"
              id="input-2"
              v-model="form.lastName"
              placeholder="Escriu els teus cognoms"
              required
              class="form-input"
            />
          </div>

          <!-- Email -->
          <div class="form-group">
            <label for="input-3">Email</label>
            <input
              type="email"
              id="input-3"
              v-model="form.email"
              placeholder="Escriu el teu email"
              required
              class="form-input"
            />
          </div>

          <!-- Codi Postal -->
          <div class="form-group">
            <label for="input-4">Codi Postal</label>
            <input
              type="text"
              id="input-4"
              v-model="form.postalCode"
              placeholder="Escriu el teu codi postal"
              required
              class="form-input"
            />
          </div>

          <!-- Botó Subscriu -->
          <button type="submit" class="subscribe-button" role="button">Subscriu</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        firstName: '',
        lastName: '',
        email: '',
        postalCode: ''
      },
      show: true,
      isDisabled: false,
      countdown: 10, 
      timerActive: true 
    };
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      alert(`Subscriure amb: ${JSON.stringify(this.form)}`);
    },
    startTimer() {
      this.isDisabled = true; 
      const timerInterval = setInterval(() => {
        if (this.countdown > 0) {
          this.countdown -= 1;
        } else {
          clearInterval(timerInterval);
          this.timerActive = false; 
        }
      }, 1000);
    },
    onReset(event) {
      event.preventDefault();
      this.form.firstName = '';
      this.form.lastName = '';
      this.form.email = '';
      this.form.postalCode = '';
    }
  },
  mounted() {
    this.startTimer();
  }
};
</script>

<style scoped>

.form-container {
  display: flex;
  justify-content: center;
  height: auto;
  background-color: #ffffff;
  padding: 2% 0;
}

.form-card {
  width: 100%;
  max-width: 400px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #f8f8f8;
  padding: 20px;
  margin: 0 auto;
}

.form-header {
  font-size: 1.5rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.form-input {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.form-input:focus {
  outline: none;
  border-color: #0066ff;
}

.subscribe-button {
  width: 100%;
  padding: 10px;
  background-color: #0066ff;
  border: none;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.gris{
  color: #a3a3a3;
}

.vermell{
  color: #ff0000;
}

@media (max-width: 576px) {
  .form-card {
    width: 100%;
    padding: 15px;
  }
}
</style>
